<template>
  <div id="mmain">
    <el-card class="box-card" shadow="hover" id="today">
      <div id="today"> {{today}} </div>
    </el-card>
    <el-card class="box-card" shadow="hover" id="taskmain">
      <el-row>
        <el-col>
          <el-input placeholder="Task Name (Enter to Add)" 
            v-model="entertask" @keyup.enter.native="addtask" clearable> </el-input>
        </el-col>
      </el-row>
      <el-row>
        <el-col>
          <el-alert
					  title="Task Example"
					  @close="deletetask"> </el-alert>
          <el-alert
					  title="Task Example"
					  @close="deletetask"> </el-alert>
        </el-col>
      </el-row>
		</el-card>
    <el-card class="box-card" shadow="hover" id="ttimer">
			<timer></timer>
		</el-card>
    <el-card class="box-card" shadow="hover" id="ptimer">
			<breaktimer></breaktimer>
		</el-card>
    <a @click="logout">Log out</a>
		<br>
  </div>
</template>

<script>
import AV from 'leancloud-storage';
import timer from '../components/timer.vue'
import breaktimer from '../components/breaktimer.vue'

if(AV.User.current){

}else{
  this.$router.push('register'); 
}

export default {
  name: 'main',
  components: {
    timer,
    breaktimer,
  },
  data() {
    return {
      today: "Wow, it is Sunday! Isn't it?",
      entertask: '',
    };
  },
  methods: {
    addtask() {
      alert("Task Added:" + this.entertask);
    },
    deletetask() {
      alert("Task Deleted");
    },
    logout() {
      this.$router.push('register'); 
    },
  },
};


</script>
<style>
  .el-card {
    margin-bottom: 10px;
  }
  .el-card:last-child {
      margin-bottom: 0;
  }
  .el-row {
    margin-bottom: 10px;
  }
  .el-row:last-child {
      margin-bottom: 0;
  }
  .el-col {
    border-radius: 4px;
  }
  .el-alert {
    margin-bottom: 8px;
  }
  .el-alert:last-child {
      margin-bottom: 0;
  }
</style>



